.footer {
  background: lightgray;
  width: 100%;
  text-align: center;
  padding: 10px;
}

@media only screen and (max-width: 480px) {
  * {
    display: flex;
    flex-direction: column;
  }
  h1,
  .cv-container h1 {
    font-size: 13px;
  }
  h2 {
    font-size: 12px;
  }
  p,
  .cv-container p {
    font-size: 8px;
  }
  .img {
    width: 50px;
    height: 60px;
  }
  button {
    align-items: center;
  }
}
